
import { defineComponent, reactive, toRefs } from "vue";
interface stateType {
  show?: boolean;
  msg?: string;
}
const state = reactive<stateType>({
  show: false,
  msg: "",
});
export const $message = (msg: string): void => {
  state.show = true;
  state.msg = msg;
  setTimeout(() => {
    state.show = false;
    state.msg = "";
  }, 2000);
};
export default defineComponent({
  name: "Message",
  setup() {
    const { show, msg } = toRefs(state);
    return { show, msg };
  },
});
